.parameter {
  margin-bottom: 10px;
  font-size: 15px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.parameter-input-value {
  display: grid;
  grid-template-columns: 50% 50%;
}

.parameter-input-value input {
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

input[disabled] {
  cursor: not-allowed;
}



